<template>
<div id='info'>
  <mu-appbar title="" class='appbar' titleClass="appbar-title" />
    <template>
      <div class="info-edit-form">
        <div class="avatar-edit-wrap">
          <mu-avatar :src='m_avatar' class="avatar-wrap" :size='80' />
          <br>
          <mu-flat-button class="avatar-button" primary type='file' labelClass='register-button' label=''>
            <input type="file" class="file-button" @change='f_upload_avatar($event)'>
          </mu-flat-button>
        </div>
        <mu-text-field label='' v-model='m_nickname' labelClass="input-label" inputClass='input-content'  fullWidth/>
        <mu-text-field label='' v-model='m_password' labelClass="input-label" inputClass='input-content'  type='password' hintText='' fullWidth/>
        <mu-text-field label='' v-model='m_password_again' labelClass="input-label" inputClass='input-content'  type='password' fullWidth/>
        <p class=tip></p>
        <div class="button-wrap" >
          <mu-raised-button class="info-edit-button"  @click='f_edit' label="" primary />
        </div>
      </div>
    </template>
</div>
</template>

<script>
export default {
  name: 'info',
  data () {
    return {
      activeStep: 0,
      m_avatar: '',
      m_nickname: '',
      m_password: '',
      m_password_again: ''
    }
  },
  mounted() {
    this.is_login().then(function (data) {
      if (data.status == 'unlogin') {
        this.$warn('', function () {
          this.$showRegisterPanel(0, function () {
            this.f_get_self_info()
          }.bind(this))
        }.bind(this), 500)
      } else if (data.status == 'ok') {
        this.f_get_self_info()
      }
    })
  },
  methods: {
    f_get_self_info () {
      this.fetch_self_info().then(function (data) {
        let result = data.result
        this.m_avatar = result.avatar
        this.m_nickname = result.nickname
      })
    },
    f_upload_avatar (event) {
      let file = event.target.files[0]
      if (['gif', 'jpg', 'jpeg', 'png'].indexOf(file.type.split('/')[1].toLowerCase()) === -1) {
        return this.$warn('')
      }
      if(file.size/1024 > 4096){
        return this.$warn.log("4Mb")
      }
       this.upload_avatar(file).then(function (data) {
         if (data.status == 'ok') {
           this.m_avatar = data.result
           this.$warn('')
         }
       })
    },
    f_edit () {
      if(this.m_avatar == '') {
        this.$warn('')
        return
      }

      // if (this.m_nickname.trim() == '') {
      //   this.$warn('')
      //   return
      // }
      // if (this.m_password.trim() == '') {
      //   this.$warn('')
      //   return
      // }
      // if(this.m_password.trim() != this.m_password_again) {
      //   this.$warn('')
      //   return
      // }

      this.edit_user({
        nickname: this.m_nickname,
        password: this.m_password
      }).then(function (data) {
        if (data.status == 'unlogin') {
          this.$warn('')
        }else if (data.status == 'ok'){
          this.$warn('',function () {
            this.$router.push('/user')
          }.bind(this))
        }
      })
    }
  }
}
</script>

<style lang="scss">
@import '../../../scss/_variables.scss';
#info{
  height: 100%;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
  padding-bottom: 10px;
  .appbar{
    height: 50px;
    .appbar-title{
      text-align: center;
      font-size: 16px;
    }
  }
  .avatar-edit-wrap{
    text-align: center;
  }
  .avatar-button{
    .file-button{
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      opacity: 0;
    }
  }
  .info-edit-form{
    margin-top: 10px;
    padding: 10px 30px;
    background-color: #fff;
    .input-content{
      font-size: 14px;
      color: $primary-color;
    }
    .mu-text-field-hint{
      font-size: 14px;
    }
    .tip{
      color: #666;
      font-size: 12px;
      margin-bottom: 10px;
      margin-top: -10px;
    }
  }
  .button-wrap{
    text-align: center;
    padding: 10px;
    .info-edit-button{
      width: 200px;
      border-radius: 20px;
      height: 40px;
    }
  }
}

</style>



