<template>
  <transition name='card-fade'>
    <div id="message-card" clas='message-card'>
      <template>
        <img v-if='data.result==1' src="../../../assets/agree.png" class="status-img" alt="">
        <img v-else-if='data.result==2' src="../../../assets/reject.png" class="status-img" alt="">
        <img v-else-if='data.result==3' src="../../../assets/over.png" class="status-img" alt="">
      </template>
      <div class="card-top">
        <div class="card-tag txt-small txt-center">
          <p></p>
          <p>By</p>
        </div>
        <div class="top-line"></div>
        <div class="card-content">
          <p class="txt-center">
            <span class="tip"></span>
            <span class="name txt-big txt-em">{{data.toUserName}}</span>
            <span class="tip"></span>
          </p>
          <p class="txt-em">{{data.skillTitle}}</p>
          <p class="intro"><span class="tip"></span>{{data.orderIntroduction}}</p>
          <p class='time txt-small txt-right'>{{data.createTime | timestampFormat}}</p>
        </div>
      </div>
      <div class="card-bottom">
        <!--  -->
        <div v-if="data.result==0" class="status-wrap">
          <p class='txt-center'> <span class="name txt-em">{{data.toUserName}}</span> </p>
        </div>
        <!--  -->
        <div v-else-if="data.result==1" class="status-wrap">
          <p class='txt-center'>
            <span class="name txt-em">{{data.toUserName}}</span>  <span class='txt-big txt-em status'></span> </p>
          <p>
            <span class="contact txt-em">{{data.toUserWeChat}}</span> <span class="name txt-em">{{data.toUserName}}</span> 
          </p>
          <p>
            <span class="txt-small"></span>
            <mu-flat-button label='' labelClass='button-over-label' @click='f_open_finish_dialog' primary />
          </p>
        </div>
        <!--  -->
        <div v-else-if="data.result==2" class="status-wrap">
          <p class='txt-center'> <span class="name txt-em">{{data.toUserName}}</span>  <span class='txt-big txt-em status'></span> </p>
          <p><span class='reject-reason txt-small'></span>{{data.rejectReason}}</p>
        </div>
        <!--  -->
        <div v-else="data.result==3" class="status-wrap">
          <p class='txt-center'> <span class='txt-big txt-em status'></span> </p>
          <p> <span class='reject-reason txt-small'></span>{{data.commentContent}}</p>
        </div>
      </div>
      <div class="card-footer txt-center txt-small">
          <p class='power-by'>POWERED BY BINGYAN STUDIO</p>
      </div>
      <mu-dialog dialogClass="reject-reason" bodyClass='dialog-body' :open='m_finish_dialog'>
        <mu-text-field v-model='m_comment' label='' inputClass="reject-input" multiLine fullWidth :rows="2" :rowsMax="4" :maxLength="100"/>
        <div class="score-wrap">
          <p class="score-title"> (<span class="txt-big txt-em txt-primary">{{m_score}}</span>)</p>
          <mu-slider class="score-slider" :max=10 :min=1 :step=1 v-model='m_score' />
        </div>
        <mu-flat-button label="" slot="actions" @click='f_close_finish_dialog' primary/>
        <mu-flat-button label="" slot="actions" @click='f_comment(data.orderId, data.skillId)' primary/>
      </mu-dialog>
    </div>
  </transition>
</template>
<script>
export default {
  name: "reseive-card",
  data: function data() {
    return {
      m_finish_dialog: false,
      m_comment: '',
      m_score: 10
    }
  },
  props: ['data'],
  methods:{
    f_open_finish_dialog(){
      this.m_finish_dialog = true
    },
    f_close_finish_dialog(){
      this.m_finish_dialog = false
    },
    f_comment(orderId, skillId) {
      if (this.m_comment.trim() == '') {
        this.$warn('')
        return
      }
      this.add_comment({
        content: this.m_comment,
        skillId: skillId,
        score: this.m_score,
        orderId: orderId
      }).then(function (data) {
        if (data.status == 'ok') {
          this.finish_order(orderId).then(function (data) {
            if (data.status == 'ok') {
              this.m_finish_dialog = false
              data.result = 3
            } else {
              this.$warn(data.message)
            }
          })
        } else {
          this.$warn(data.message)
        }
      })
    }
  }
}
</script>
<style lang="scss">
@import '../../../scss/message.scss';
</style>



