<template>
  <transition name='card-fade'>
    <div id="message-card" clas='message-card'>
      <template>
        <img v-if='data.result==1' src="../../../assets/agree.png" class="status-img" alt="">
        <img v-else-if='data.result==2' src="../../../assets/reject.png" class="status-img" alt="">
        <img v-else-if='data.result==3' src="../../../assets/over.png" class="status-img" alt="">
      </template>
      <div class="card-top">
        <div class="card-tag txt-small txt-center">
          <p></p>
          <p>By</p>
        </div>
        <div class="top-line"></div>
        <div class="card-content">
          <p class="txt-center"><span class="name txt-big txt-em">{{data.fromUserNickname}}</span> <span class="tip"></span></p>
          <p class="txt-em">{{data.skillTitle}}</p>
          <p class="intro"><span class="tip"></span>{{data.orderIntroduction}}</p>
          <p class='time txt-small txt-right'>{{data.createTime | timestampFormat}}</p>
        </div>
      </div>
      <div class="card-bottom">
        <div v-if="data.result==0" class="button-wrap">
          <mu-raised-button label='' class='button' labelClass='button-label' @click='f_open_reject_dialog' primary />
          <mu-raised-button label='' class='button' labelClass='button-label' @click='f_open_agree_dialog' primary />
        </div>
        <div v-else-if="data.result==1" class="status-wrap">
          <p class='txt-center'> <span class='txt-big txt-em status'></span> </p>
          <p><span class="contact txt-em">{{data.fromUserWeChat}}</span><span class="name txt-em">  </span></p>
          <!-- <p>
            <span class="txt-small"></span>
            <mu-flat-button label='' labelClass='button-over-label' @click='f_over(data.orderId)' primary />
          </p> -->
        </div>
        <div v-else-if="data.result==2" class="status-wrap">
          <p class='txt-center'> <span class='txt-big txt-em status'></span> </p>
          <p> <span class='reject-reason txt-small'></span>{{data.rejectReason}}</p>
        </div>
        <div v-else="data.result==3" class="status-wrap">
          <p class='txt-center'> <span class='txt-big txt-em status'></span> </p>
          <p> <span class='reject-reason txt-small'>{{data.fromUserNickname}}</span>{{data.commentContent}}</p>
        </div>
      </div>
      <div class="card-footer txt-center txt-small">
          <p class='power-by'>POWERED BY BINGYAN STUDIO</p>
      </div>
      <mu-dialog dialogClass="reject-reason" bodyClass='dialog-body' :open='m_reject_dialog'>
        <mu-text-field label='' v-model='m_reject_reason' inputClass="reject-input" multiLine fullWidth :rows="2" :rowsMax="4" :maxLength="100"/>
        <mu-flat-button label="" slot="actions" @click='f_close_reject_dialog' primary/>
        <mu-flat-button label="" slot="actions" @click='f_reject(data.orderId)' primary/>
      </mu-dialog>
      <mu-dialog dialogClass="agree-dialog" bodyClass='dialog-body' :open='m_agree_dialog'>
        <p></p>
        <mu-flat-button label="" slot="actions" @click='f_close_agree_dialog' primary/>
        <mu-flat-button label="" slot="actions" @click='f_agree(data.orderId)' primary/>
      </mu-dialog>
    </div>
  </transition>
</template>
<script>
export default {
  name: "reseive-card",
  data: function data() {
    return {
      m_reject_dialog: false,
      m_agree_dialog: false,
      m_reject_reason: '',
      m_status: data.result
    }
  },
  props: ['data'],
  methods:{
    f_reject: function (orderId) {
      if (this.m_reject_reason.trim() == '') {
        this.$warn('')
        return
      }
      this.reject_order(orderId, {
        rejectReason: this.m_reject_reason
      }).then(function (data) {
        if (data.status == 'ok') {
          this.data.result = 2
          this.f_close_reject_dialog()
        } else {
          this.$warn(data.message)
        }
      })
    },
    f_agree: function (orderId) {
      this.agree_order(orderId).then(function (data) {
        if (data.status == 'ok') {
          this.data.result = 1
          this.f_close_agree_dialog()
        } else {
          this.$warn(data.message)
        }
      })
    },
    f_over: function (orderId) {
      this.finish_order(orderId).then(function (data) {
        if (data.status == 'ok') {
          this.data.result = 3
        } else {
          this.$warn(data.message)
        }
      })
    },
    f_open_reject_dialog: function () {
      this.m_reject_dialog = true
    },
    f_close_reject_dialog: function () {
      this.m_reject_dialog = false
    },
    f_open_agree_dialog: function () {
      this.m_agree_dialog = true
    },
    f_close_agree_dialog: function () {
      this.m_agree_dialog = false
    }
  }
}
</script>
<style lang="scss">
@import '../../../scss/message.scss';
</style>



